.youtube-embed-block {
  border-radius: 8px;
  margin: 16px 0;
  transition: all .2s ease-in-out;
  overflow: hidden;
}

.youtube-embed-block:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -2px #0000000d;
}

.youtube-embed-block.focused {
  ring: 2px;
  ring-color: #3b82f680;
  ring-offset: 2px;
  border-color: #3b82f6;
}

.youtube-embed-loading {
  color: #6b7280;
  background-color: #f3f4f6;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  height: 315px;
  font-size: 14px;
  display: flex;
}

.youtube-embed-loading:before {
  content: "Loading YouTube video...";
}

.youtube-embed-error {
  color: #dc2626;
  text-align: center;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  height: 200px;
  padding: 20px;
  font-size: 14px;
  display: flex;
}

.youtube-embed-error:before {
  content: " Failed to load YouTube video. Please check the URL and try again.";
}

@media (width <= 640px) {
  .youtube-embed-block {
    border-radius: 8px;
    margin: 12px 0;
  }

  .youtube-embed-container {
    border-radius: 8px;
    max-width: 100%;
    margin: 1rem auto;
  }

  .youtube-embed-container iframe {
    border-radius: 8px;
  }
}

@media (width >= 641px) and (width <= 1024px) {
  .youtube-embed-container {
    max-width: 700px;
    margin: 1.5rem auto;
  }
}

@media (width >= 1025px) {
  .youtube-embed-container {
    max-width: 800px;
    margin: 2rem auto;
  }
}

@media (width >= 1200px) {
  .youtube-embed-container {
    max-width: 900px;
    margin: 2.5rem auto;
  }
}

@media print {
  .youtube-embed-block {
    break-inside: avoid;
    page-break-inside: avoid;
  }

  .youtube-embed-container:after {
    content: "YouTube Video: https://www.youtube.com/watch?v=" attr(data-video-id);
    color: #666;
    text-align: center;
    margin-top: 8px;
    font-size: 12px;
    display: block;
  }
}

